const mutations = {
    // 
    switchLang(state, lang) {
        state.currentLang = lang
        Vue.config.lang = lang
        document.cookie = "VR_LANG=" + lang + "; path=/;domain=.snail.com"
        // location.reload()
    },
    //
    setPageName(state, name) {
        state.currentPageName = name
    },
    // 
    // setBackPageName(state, name) {
    //     state.backPageName = name
    // },
    // search / 
    toggleHeaderStatus(state, status) {
        state.headerStatus = status
    },
    //
    toggleTipsStatus(state, status) {
        if (status == -1) {
            state.tipsStatus = false
        } else {
            state.tipsStatus = !state.tipsStatus
        }
    },
    //
    addNewMsg(state) {
        state.newMsgCount > 99 ? state.newMsgCount = "99+" : state.newMsgCount++
    },
    //
    minusNewMsg(state) {
        state.newMsgCount < 1 ? state.newMsgCount = 0 : state.newMsgCount--
    },
    // 
    setMsgStick(state, mid) {

    },
    // 
    cancelMsgStick(state, mid) {

    },
    //
    getBaseMsg(state, value) {
        // console.log(value)
        state.msgList.baseMsg = value
    },
    //
    updateBaseMsg(state, value) {
        if (value.data.msgType == "text") {
            state.msgList.baseMsg.map(item => {
                if (item.roomId == value.data.roomId) {
                    item.msg[0].msg = value.data.msg
                    item.time = value.data.time
                }
            })
        } else if (value.data.msgType == "img") {
            state.msgList.baseMsg.map(item => {
                if (item.roomId == value.data.roomId) {
                    item.msg[0].msg = "[]"
                    item.time = value.data.time
                }
            })
        }
        let num = 0;
        state.msgList.baseMsg.map(item => {
            if (item.top == true) {
                num++;
                if (item.roomId == value.data.roomId) {
                    state.msgList.baseMsg.splice(0, 0, item);
                }
            }
            if (item.top == false) {
                if (item.roomId == value.data.roomId) {
                    state.msgList.baseMsg.splice(num, 0, item);
                }
            }
        })
        //
        let hash = {};
        state.msgList.baseMsg = state.msgList.baseMsg.reduce((preVal, curVal) => {
            hash[curVal.roomId] ? '' : hash[curVal.roomId] = true && preVal.push(curVal);
            return preVal
        }, [])
    },
    //
    updateBaseMsgList(state, value) {
        // console.log(value)
        //
        state.msgList.baseMsg.map(item => {
            if (item.roomId == value.roomId) {
                item = value
            } else {
                state.msgList.baseMsg.push(value)
            }
            //
            let hash = {};
            state.msgList.baseMsg = state.msgList.baseMsg.reduce((preVal, curVal) => {
                hash[curVal.roomId] ? '' : hash[curVal.roomId] = true && preVal.push(curVal);
                return preVal
            }, [])
        })
        // console.log(state.msgList.baseMsg)

    },
    //
    getUserInfo(state, value) {
        state.userInfo = value
    },
    //
    getFrineds(state, value) {
        state.allContacts = value
    },
    //socket
    getSocket(state, val) {
        // console.log(val)
        state.msgList.baseMsg = val
    },
    //
    saveLog(state, val) {
        console.log(val)
        state.chatlog = val
    },
    //
    addChat(state, val) {
        console.log(val)
        if (state.chatlog.msg != undefined && val.roomId == state.chatlog.roomId) {
            state.chatlog.msg.push(val)
            // console.log(state.chatlog.msg)
        }
    },
    //
    sendChat(state, val) {
        console.log(val)
        if (state.chatlog.msg != undefined) {
            state.chatlog.msg.push(val)
            // console.log(state.chatlog.msg)
        }
    },
    //
    toastMsg(state, val) {
        state.toastMsg = val
    },
    //
    pullRefresh(state, val) {
        state.chatlog.msg = val.concat(state.chatlog.msg)
    },
    //+1
    addMsgCount(state, val) {
        state.msgList.baseMsg.map(item => {
            if (item.roomId == val) {
                item.newMsgCount = item.newMsgCount + 1
            }
        })

    },
    //
    getSysExp(state, val) {
        state.sysExp = val
    }
}
export default mutations


