import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import vuex from 'vue'
import App from './App'
import router from './router'
import store from './vuex/store'
import FastClick from 'fastclick' // fastclick  300ms 
import filters from './filters' // filters/index.js 

import Vant from 'vant';
import 'vant/lib/index.css';

import global_ from './libs/index'//
Vue.prototype.$global = global_//Vue

Vue.use(Vant);

import { Popup } from 'vant';
Vue.use(Popup);

import { Image } from 'vant';
Vue.use(Image);

import tools from './assets/tools/tools.js';//js
Vue.prototype.$tools = tools;

import toastRegistry from './toast/index'
//  toastRegistry()
Vue.use(toastRegistry)

//  use  
Vue.use(VueAxios, axios, vuex)
// 
filters(Vue)

Vue.config.productionTip = false // false , Vue 

FastClick.attach(document.body)

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
})
